@import url(https://fonts.googleapis.com/css?family=Roboto:300);
:root {
    --basic-font-family: -apple-system, BlinkMacSystemFont, Roboto, sans-serif;
    --basic-code-font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
    --default-font-weight: 300;

    --basic-font-color: #000000;
    --positive-color: #388e3c;
    --negative-color: #ff1e23;

}
.form-container {
    width: 100%;
    max-width: 600px;
    flex-grow: 1;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;
}

.form-container > form {
    flex-grow: 1;
}

@media screen and (min-width: 600px) {
    .form-container {
        margin-top: 64px;
        flex-grow: 0;
    }
}



form {
    display: flex;
    flex-direction: column;
}

.form-header {
    font-size: 2em;
    text-align: center;
}



.form-container > form > .form-header {
    padding: 24px 24px 20px 24px;
}

.form-container > form > .form-content {
    padding: 0 24px 24px 24px;
    flex-grow: 1;
}

.form-container > form > footer {
    margin: 8px 4px;
    display: flex;
    justify-content: flex-end;
}


/*LOGIN FORM*/

.login-form {
    display: flex;
    flex-direction: column;
}


/*REGISTER FORM*/

.register-form {
    display: flex;
    flex-direction: column;
}


/*ENTRY FORM*/

.new-entry-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 1em;

    grid-template-areas:
        'value category'
        'description description'
        'date date'
        'subEntries subEntries'
        'newSubEntry newSubEntry';
}

.new-entry-form .currency-input { grid-area: value; }
.new-entry-form .category-input { grid-area: category; }
.new-entry-form .description-input { grid-area: description; }
.new-entry-form .date-input { grid-area: date; }
.new-entry-form .subEntry-area { grid-area: subEntries; }
.new-entry-form .new-subEntry-btn { grid-area: newSubEntry; }
.new-entry-form .remove-subEntry-btn { grid-area: removeSubEntry; align-self: end; }
.new-entry-form .subEntry-area .subEntry-row {
    display: grid;
    grid-template-columns: 1fr 1fr 48px;
    grid-column-gap: 1em;

    grid-template-areas:
        'value category removeSubEntry'
        'description description description';

    margin-top: 16px;
}

@media screen and (min-width: 600px) {
    .new-entry-form .subEntry-area .subEntry-row {
        grid-template-columns: 1fr 1fr 1fr 48px;

        grid-template-areas:
            'value description category removeSubEntry';
    }
}

/*CATEGORY FORM*/

.category-form {
    display: flex;
    flex-direction: column;
}


/*ENTRIES SELECTION FORM*/

.selection-entries-form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 1em;

    grid-template-areas:
        'from to'
        'categories categories';
}
.selection-entries-form .from-input {
    grid-area: from;
    margin-left: 16px;
    margin-right: 8px;
}
.selection-entries-form .to-input {
    grid-area: to;
    margin-left: 8px;
    margin-right: 16px;
}
.selection-entries-form .categories-input {
    grid-area: categories;
}
.entry-list {
    width: 100%;
    max-width: 960px;
}

.entries-for-day.header {
    font-size: 1.2em;
    padding: 12px 0 12px 24px;
}


.category {
    font-style: italic;
}
.description {
    font-size: 1.1em;
}
.value {
    font-size: 1.1em;
    display: flex;
}
.currency {
    margin-left: 0.5em;
    font-size: 0.6em;
    color: var(--basic-font-color);
}


.entry-row-summary {
    width: 100%;

    display: grid;
    grid-template-columns: auto minmax(10px, 1fr) auto;

    grid-template-areas:
        'time category value'
        'description description value';
}

.entry-row-summary .time {
    grid-area: time;
}
.entry-row-summary .category {
    grid-area: category;

    margin-left: 1em;
}
.entry-row-summary .description {
    grid-area: description;
}
.entry-row-summary .value {
    grid-area: value;

    align-self: center;
    justify-self: end;

    margin-left: 1em;
}



.entry-row-details {
    width: 100%;

    display: grid;
    grid-template-columns: minmax(10px, 1fr) auto;

    grid-template-areas:
        'subEntriesArea subEntriesArea'
        'added added'
        'modified modified';
}

.entry-row-details .addition-date {
    grid-area: added;
}
.entry-row-details .last-modification-date {
    grid-area: modified;
}
.entry-row-details .sub-entries-area {
    grid-area: subEntriesArea;

    margin: 0 1em 1em 1em;

    display: grid;
    grid-template-columns: auto auto 1fr;
    grid-column-gap: 1em;
    grid-row-gap: 0.4em;
}

.sub-entries-area .value  {
    justify-self: end;
}

.chart-area {
    flex-grow: 1;

    height: 100%;
    min-height: 300px;
    max-height: 600px;

    width: 100%;
    max-width: 960px;

    margin-top: 12px;

    display: flex;
    flex-wrap: wrap;
}

.hourly-chart-tooltip {
    display: grid !important;
    grid-template-columns: auto minmax(10px, 1fr);

    grid-template-areas:
        'date category'
        'description description'
        'value value';
}

.hourly-chart-tooltip .date { grid-area: date; }
.hourly-chart-tooltip .category { grid-area: category; margin-left: 1em; }
.hourly-chart-tooltip .description { grid-area: description; }
.hourly-chart-tooltip .value { grid-area: value; }



.line-chart-tooltip {
}

.line-chart-tooltip .data-summary {
    display: grid !important;
    grid-template-columns: minmax(10px, 1fr) auto;
}

.line-chart-tooltip .data-summary .category { margin-left: 1em; }

* , *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-weight: var(--default-font-weight);
}

body {
    font-family: var(--basic-font-family);
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
}


#root {
    height: 100%;
    display: flex;
}


.m-0 {
    margin: 0 !important;
}

.positive {
    color: var(--positive-color);
}

.negative {
    color: var(--negative-color);
}

